.c-button {
  position: relative;
  min-height: 3.5rem;
}

.c-button--margin {
  margin-top: 3.5rem;
}

.c-button--height {
  min-height: 4.5rem;
}

.c-button__link {
  background-color: #fff;
  border: 2px solid #dc3001;
  border-radius: 9999px;
  color: #dc3001;
  display: inline-grid;
  min-height: 3.5rem;
  min-width: 15.5rem;
  padding: 0 1.5rem;
  place-items: center;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  font-weight: bold;
  text-align: center;
}

.c-button__link--big {
  min-width: 19.5rem;
}

.c-button__link--small {
  min-width: 14rem;
}

.c-button__link--borer-white {
  border: 2px solid #fff;
}

.c-button__link--blue {
  color: #112242;
}

.c-button__link--tall {
  min-height: 4.5rem;
}

.c-button__circle {
  background-color: #dc3001;
  border-radius: 9999px;
  color: #fff;
  display: grid;
  height: 2.25rem;
  left: calc(2.25rem / 4);
  place-items: center;
  position: absolute;
  top: calc(50% - 2.25rem / 2);
  width: 2.25rem;
}
.c-button__circle::after {
  content: "";
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 0.625rem;
  height: 0.625rem;
  margin-right: 0.25rem;
  border-top: 2px solid #fff;
  border-right: 2px solid #fff;
  transform: rotate(45deg);
}

.c-button__circle--blue {
  background-color: #112242;
}

.c-button__circle--big {
  width: 3rem;
  height: 3rem;
  top: calc(50% - 3rem / 2);
}

.c-button__circle--email::after {
  width: 1.5rem;
  height: 1.375rem;
  margin-right: 0;
  transform: rotate(0);
  border: none;
  background: url("../images/contact_email.svg") center/100% no-repeat;
}

.c-button__circle--calendar::after {
  width: 1.5rem;
  height: 1.375rem;
  margin-right: 0;
  transform: rotate(0);
  border: none;
  background: url("../images/contact_calendar.svg") center/100% no-repeat;
}

